<template>
  <el-dialog
    v-bind="Object.assign({}, defaultAttr, attr)"
    :visible.sync="show"
    v-on="$listeners"
  >
    <template slot="title">
      <slot name="header">
        <span class="el-dialog__title">{{
          attr ? attr.title : "" || defaultAttr.title
        }}</span>
      </slot>
    </template>
    <slot></slot>
    <div
      v-if="showFooter"
      slot="footer"
      class="dialog-footer"
      :align="footer.align ? footer.align : 'right'"
    >
      <slot name="footer">
        <el-button type="info" @click="cancel"></el-button>
        <el-button type="primary" @click="confirm"></el-button>
      </slot>
    </div>
  </el-dialog>
</template>
<script>
export default {
  name: "VeDialog",
  props: {
    visible: Boolean, // 
    footer: Object, // 
    confirmFunc: Function, // 
    attr: Object,
  },
  data() {
    return {
      show: false,
      defaultAttr: {
        title: "",
        width: "500px",
        closeOnClickModal: false,
      },
    };
  },
  computed: {
    showFooter() {
      return (
        (typeof this.footer === "boolean" && this.footer) ||
        (typeof this.footer === "object" && this.footer.show)
      );
    },
  },
  methods: {
    // 
    confirm() {
      if (this.confirmFunc) {
        if (!this.confirmFunc()) return;
        this.show = false;
      }
      this.$emit("confirm");
    },
    // 
    cancel() {
      this.show = false;
    },
  },

  watch: {
    visible(val) {
      this.show = val;
    },
    show(val) {
      this.$emit("update:visible", val);
    },
  },
};
</script>

<style scoped>
.el-dialog >>> .el-dialog__body {
  padding: 20px;
}
</style>
