//browser.js
 var Browser =  (function(window) {
        var document = window.document,
            navigator = window.navigator,
            agent = navigator.userAgent.toLowerCase(),
            //IE8+.
            //IE6,IE7:undefined.IE8:8(7).IE9:9(7||8)
            //IE10:10(7||8||9)
            //IE10:11(7||8||9||10)
            IEMode = document.documentMode,
            //chorme
            chrome = window.chrome || false,
            System = {
                //user-agent
                agent: agent,
                //IE
                isIE: /(msie\s|trident.*rv:)([\w.]+)/.test(agent),
                //Gecko
                isGecko:
                    agent.indexOf("gecko") > 0 &&
                    agent.indexOf("like gecko") < 0,
                //webkit
                isWebkit: agent.indexOf("webkit") > 0,
                //
                isStrict: document.compatMode === "CSS1Compat",
                //subtitle
                supportSubTitle: function() {
                    return "track" in document.createElement("track");
                },
                //scoped
                supportScope: function() {
                    return "scoped" in document.createElement("style");
                },
                //IE
                ieVersion: function() {
                    try {
                        return agent.match(/msie ([\d.]+)/)[1] || 0;
                    } catch (e) {
                        return IEMode;
                    }
                },
                //Opera
                operaVersion: function() {
                    try {
                        if (window.opera) {
                            return agent.match(/opera.([\d.]+)/)[1];
                        } else if (agent.indexOf("opr") > 0) {
                            return agent.match(/opr\/([\d.]+)/)[1];
                        }
                    } catch (e) {
                        console.log("error");
                        return 0;
                    }
                },
                //:version.31.0.252.152 31.0
                versionFilter: function() {
                    if (
                        arguments.length === 1 &&
                        typeof arguments[0] === "string"
                    ) {
                        var version = arguments[0];
                        var start = version.indexOf(".");
                        if (start > 0) {
                            var end = version.indexOf(".", start + 1);
                            if (end !== -1) {
                                return version.substr(0, end);
                            }
                        }
                        return version;
                    } else if (arguments.length === 1) {
                        return arguments[0];
                    }
                    return 0;
                }
            };

        try {
            //(IEOperaChromeSafariFirefox)
            System.type = System.isIE ? "IE" : window.opera || agent.indexOf("opr") > 0 ? "Opera"
                : agent.indexOf("chrome") > 0
                ? "Chrome"
                : //safari
                window.openDatabase
                ? "Safari"
                : agent.indexOf("firefox") > 0
                ? "Firefox"
                : "unknow";

            //
            System.version =
                System.type === "IE"
                    ? System.ieVersion()
                    : System.type === "Firefox"
                    ? agent.match(/firefox\/([\d.]+)/)[1]
                    : System.type === "Chrome"
                    ? agent.match(/chrome\/([\d.]+)/)[1]
                    : System.type === "Opera"
                    ? System.operaVersion()
                    : System.type === "Safari"
                    ? agent.match(/version\/([\d.]+)/)[1]
                    : "0";

            //
            System.shell = function() {
                //
                if (agent.indexOf("maxthon") > 0) {
                    System.version =
                        agent.match(/maxthon\/([\d.]+)/)[1] || System.version;
                    return "";
                }
                //QQ
                if (agent.indexOf("qqbrowser") > 0) {
                    System.version =
                        agent.match(/qqbrowser\/([\d.]+)/)[1] || System.version;
                    return "QQ";
                }

                //
                if (agent.indexOf("se 2.x") > 0) {
                    return "";
                }

                //Chrome:window.chrome && window.chrome.webstore
                if (chrome && System.type !== "Opera") {
                    var external = window.external,
                        clientInfo = window.clientInformation,
                        //:zh-cn,zh.360undefined
                        clientLanguage = clientInfo.languages;

                    //:agent.indexOf("lbbrowser")>0
                    if (external && "LiebaoGetVersion" in external) {
                        return "";
                    }
                    //
                    if (agent.indexOf("bidubrowser") > 0) {
                        System.version =
                            agent.match(/bidubrowser\/([\d.]+)/)[1] ||
                            agent.match(/chrome\/([\d.]+)/)[1];
                        return "";
                    }
                    //360360
                    if (
                        System.supportSubTitle() &&
                        typeof clientLanguage === "undefined"
                    ) {
                        //object.key().
                        var storeKeyLen = Object.keys(chrome.webstore).length
                        return storeKeyLen > 1
                            ? "360"
                            : "360";
                    }
                    return "Chrome";
                }
                return System.type;
            };

            //(,)
            System.name = System.shell();
            //
            System.version = System.versionFilter(System.version);
        } catch (e) {
            console.log(e);
        }
        return {
            client: System
        };
    })(window);

export default Browser;
